*, *::before, *::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    margin: 0;
    padding: 0;
    background: #eee;
}


.bgImage{
    position: fixed;
    display: block;
    z-index: -1;
    filter: brightness(40%);
    height: 100%;
    width: 100%;
    object-fit: cover;
}
/* hr {
    border-top: 2px solid rgb(255, 255, 255);
} */

.navbar{
    /***** stay on top *****/
    position: -webkit-sticky;
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 5px 15px;
    /*margin: auto;*/
    /*background: rgb(118, 112, 206);*/
    background-image:  linear-gradient(to right, rgb(25, 25, 25), rgba(25, 25, 25, 0.3));
}
.navbar p{
    color: #eee;
    font-size: 20px;
    /*text-shadow: 1px -1px rgb(226, 214, 214);*/
}

.navbarlinks{
    width: 30%;
    display: flex;
    justify-content: space-between;
}

.navitem{
    margin: auto;
    padding: 0;
    color: #eee;
    font-size: 22px;
    /*text-shadow: 1px -1px rgb(226, 214, 214);*/
}

.navitem p::after{
    content: "";
    display: block;
    height: 2px;
    width: 0px;
    background: rgb(238, 238, 238);
    transition: width ease-out 300ms;
}

.navitem p:hover::after{
    width: 100%;
}

.navitem p:hover{
    /*transform: scale(1.1);*/
    color: rgb(194, 194, 194);
    /*text-decoration-line: underline;*/
    cursor: pointer;
}

.logoicon{
    height: 50px;
    width: 65px;
    padding: 5px 15px;
}

.logolink{
    height: 50px;
    width: 50px;
    padding: 5px 5px;
    margin: 0px 10px;
    border-radius: 30%;
    background-color: rgba(255, 255, 255, 0.25);
    color: rgb(0, 0, 100);
    /*filter: drop-shadow(1px 1px rgba(170, 170, 170, 0.3)) drop-shadow(-1px 1px rgba(170, 170, 170, 0.3)) drop-shadow(1px -1px rgba(170, 170, 170, 0.3)) drop-shadow(-1px -1px rgba(170, 170, 170, 0.3));*/
    transition: transform ease-out 300ms;
}

.logolink:hover{
    transform: scale(1.3);
    /*color: rgb(0, 0, 200, 0.5);*/
    /*filter: drop-shadow(0px 3px grey);*/
}

/********** Add tooltip ***********/
[tooltip]{
    position: relative;
}

[tooltip]::after{
    position: absolute;
    margin: 0 auto;
    display: block;
    content: attr(tooltip);
    border-radius: 5px;
    padding: 5px;
    background: rgb(0, 0, 105, 0.6);
    width: fit-content;
    color: honeydew;
    
    bottom: -30px;
    left: -50%;

    white-space: nowrap; /*ser till att allt kommer p√• en rad*/
    /*animation*/
    transform: scale(0);
    transition: transform ease-out 400ms;
}
[tooltip]:hover::after{
    transform: scale(1);
}

